.heroBanner {
  &__wrap {
    position: relative;
    height: calc(100vh - 8rem);
    overflow: hidden;
    background-image: url(../images/landing-banner.jpg?v=#{$image-version});
    background-repeat: no-repeat;
    background-position: center center;
    @include background-size(cover);
  }

  &__cta {
    text-align: center;
    position: absolute;
    top: auto;
    left: 0;
    right: 0;
    bottom: 3rem;

    a {
      height: 4rem;
      display: inline-block;

      @media screen and (min-width: $media-desktop-min) {
        height: 6rem;
      }

      img {
        display: block;
        height: 100%;
        filter: drop-shadow(3px 3px 3px rgba(var(--black-rgb), .4));
      }
    }
  }
}

.mint {
  &__wrap {}

  &__box {
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    @extend %flexbox !optional;

    @media screen and (min-width: $media-desktop-min) {
      flex-direction: row;
    }

    &-lft,
    &-rgt {
      // width: 100%;
      // flex-grow: 1;
      padding-left: var(--half-space);

      @media screen and (min-width: $media-desktop-min) {
        padding-right: var(--full-space);
        padding-left: var(--full-space);
      }
    }

    &-rgt {
      padding-top: calc(var(--full-space)*2);
      padding-left: 0;

      @media screen and (min-width: $media-desktop-min) {
        flex-grow: 1;
        padding-top: 0;
        padding-left: calc(var(--full-space)*4);
      }
    }
  }

  &__thumb {
    @include box-shadow(var(--half-space-negative), var(--half-space), 0, 0, var(--black));

    @media screen and (min-width: $media-desktop-min) {
      @include box-shadow(var(--full-space-negative), var(--full-space), 0, 0, var(--black));
    }

    img {
      width: 100%;
      display: block;
    }
  }

  &__intro {
    padding-bottom: var(--full-space);
    justify-content: flex-start;
    flex-direction: column;
    @extend %flexbox !optional;

    @media screen and (min-width: $media-desktop-min) {
      padding-bottom: calc(var(--full-space)*2);
      justify-content: space-between;
      align-items: center;
      flex-direction: row;
    }

    .btn {
      margin-top: var(--full-space);
      margin-left: 0;

      @media screen and (min-width: $media-desktop-min) {
        margin-top: 0;
      }
    }

    p {
      color: var(--white);
    }
  }

  &__counter,
  &__cta {
    height: 5rem;
    background-color: var(--secondary-color);
    justify-content: space-between;
    align-items: center;
    @extend %flexbox !optional;
    @extend .borRad100 !optional;

    @media screen and (min-width: $media-desktop-min) {
      height: 6rem;
      margin-bottom: calc(var(--full-space)*2);
    }
  }

  &__counter {
    margin-bottom: var(--full-space);

    &-value {
      color: var(--white);
      font-size: 1.4rem;

      @media screen and (min-width: $media-desktop-min) {
        font-size: 1.8rem;
      }
    }

    .btn {
      margin: 0;
    }
  }

  &__cta {
    padding: 0 var(--full-space);
    margin-bottom: var(--half-space);

    &-value {
      color: var(--white);
      font-size: 1.8rem;

      @media screen and (min-width: $media-desktop-min) {
        font-size: 2.2rem;
      }
    }

    .btn {
      margin: 0;
    }
  }

  &__label {
    color: var(--white);
    font-size: 1.2rem;
    text-align: right;

    @media screen and (min-width: $media-desktop-min) {
      font-size: 1.6rem;
    }
  }

  &__icon {
    @media screen and (max-width: $media-mobile-min) {
      width: 6rem;
      height: 6rem;
      text-align: center;
      margin: 0 auto var(--full-space);

      img {
        display: block;
        width: 100%;
      }
    }
  }

  &__successful {
    .section-heading {
      @media screen and (max-width: $media-mobile-min) {
        font-size: 2.8rem;
        text-align: center;
      }
    }

    .mint__intro {
      justify-content: flex-start;
      align-items: flex-start;
      flex-direction: column;

      .btn {
        margin-top: var(--full-space);
      }
    }
  }

  &__overlay {
    background-color: transparent !important;
    position: fixed;
    top: calc(var(--full-space)*7);
    left: 0;
    right: 0;
    margin: 0 auto;
    width: 100%;
    padding: 0 var(--full-space);

    &-box {
      background-color: var(--black);
      border: 1px solid var(--red);
      max-width: 600px;
      margin: 0 auto;
      padding: var(--full-space) calc(var(--full-space)*2);
      justify-content: center;
      align-items: center;
      @extend %flexbox !optional;
      width: 100%;
      color: var(--cultured);
      @extend .borRad100 !optional;
    }

    &-close {
      cursor: pointer;
      margin-right: var(--full-space);
    }
  }
}

.static {
  &__wrap {}

  &__container {}

  &__box {
    color: var(--cultured);
    font-size: 1.2rem;

    @media screen and (min-width: $media-desktop-min) {
      font-size: 1.6rem;
    }

    &:not(:last-child) {
      margin-bottom: calc(var(--full-space)*2);

      @media screen and (min-width: $media-desktop-min) {
        margin-bottom: calc(var(--full-space)*4);

      }
    }

    p {
      line-height: 1.6;

      &:not(:last-child) {
        margin-bottom: var(--full-space);
      }
    }
  }

  &__title {
    margin-bottom: var(--half-space);
    font-style: italic;
    @extend .regularFF--sec !optional;

    @media screen and (min-width: $media-desktop-min) {
      margin-bottom: var(--full-space);
    }
  }
}

.section {

  &--faq,
  &--team,
  &--howItWork {
    background-color: var(--primary-dark-color);
  }

  &--books,
  &--tier {
    background-color: var(--secondary-color);
  }
}

// tier__
.tier {
  &__list {
    ul {
      li {
        &:not(:last-child) {
          margin-bottom: calc(var(--full-space)*4);
        }

        &:not(:nth-child(odd)) {
          .tier__box {
            @media screen and (min-width: $media-desktop-min) {
              flex-direction: row-reverse;
            }
          }
        }
      }
    }
  }

  &__box {
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
    @extend %flexbox !optional;

    @media screen and (min-width: $media-desktop-min) {
      flex-direction: row;
    }

    &-lft,
    &-rgt {
      padding-left: var(--half-space);

      @media screen and (min-width: $media-desktop-min) {
        padding-right: var(--full-space);
        padding-left: var(--full-space);
      }
    }

    &-rgt {
      width: 100%;
      padding-top: calc(var(--full-space)*2);
      padding-left: 0;

      @media screen and (min-width: $media-desktop-min) {
        width: auto;
        flex-grow: 1;
        padding-top: 0;
        padding-left: calc(var(--full-space)*4);
      }
    }
  }


  &__thumb {
    &.bronze {
      @include box-shadow(var(--half-space-negative), var(--half-space), 0, 0, var(--bronze));

      @media screen and (min-width: $media-desktop-min) {
        @include box-shadow(var(--full-space-negative), var(--full-space), 0, 0, var(--bronze));
      }
    }

    &.silver {
      @include box-shadow(var(--half-space-negative), var(--half-space), 0, 0, var(--silver));

      @media screen and (min-width: $media-desktop-min) {
        @include box-shadow(var(--full-space), var(--full-space), 0, 0, var(--silver));
      }
    }

    &.gold {
      @include box-shadow(var(--half-space-negative), var(--half-space), 0, 0, var(--gold));

      @media screen and (min-width: $media-desktop-min) {
        @include box-shadow(var(--full-space-negative), var(--full-space), 0, 0, var(--gold));
      }
    }

    &.platinum {
      @include box-shadow(var(--half-space-negative), var(--half-space), 0, 0, var(--platinum));

      @media screen and (min-width: $media-desktop-min) {
        @include box-shadow(var(--full-space), var(--full-space), 0, 0, var(--platinum));
      }
    }

    img {
      width: 100%;
      display: block;
    }
  }

  &__heading {
    font-size: 2.6rem;
    color: var(--cultured);
    @extend .regularFF--sec !optional;
    margin-bottom: var(--full-space);

    @media screen and (min-width: $media-desktop-min) {
      font-size: 4.4rem;
    }
  }

  &__price {
    font-size: 1.6rem;
    color: var(--secondary-title-color);
    @extend .regularFF--sec !optional;
    margin-bottom: calc(var(--full-space)*2);

    @media screen and (min-width: $media-desktop-min) {
      font-size: 2.4rem;
    }
  }

  &__description {
    color: var(--secondary-title-color);

    p {
      font-size: 1.4rem;

      @media screen and (min-width: $media-desktop-min) {
        font-size: 1.8rem;
      }
    }

    ol {
      margin-top: var(--full-space);
      margin-left: 2rem;
      list-style: disc;
      list-style-position: outside;

      li {
        font-size: 1.4rem;

        @media screen and (min-width: $media-desktop-min) {
          font-size: 1.8rem;
        }

        &:not(:last-child) {
          margin-bottom: var(--full-space);
        }
      }
    }
  }
}

.team {
  &__list {
    ul {
      flex-wrap: wrap;
      justify-content: flex-start;
      align-items: flex-start;
      @extend %flexbox !optional;

      li {
        width: 100%;
        padding: var(--full-space);

        &:not(:last-child) {
          padding-bottom: calc(var(--full-space)*4);
        }

        @media screen and (min-width: $media-desktop-min) {
          width: calc(100%/3);
          padding-bottom: 0;

          &:not(:last-child) {
            padding-bottom: 0;
          }

          &:not(:nth-last-child(-n+3)) {
            padding-bottom: calc(var(--full-space)*4);
          }
        }
      }
    }
  }

  &__box {
    text-align: center;
  }

  &__thumb {
    width: 25rem;
    height: 25rem;
    overflow: hidden;
    margin: 0 auto;
    position: relative;
    object-fit: cover;
    background-color: var(--black);

    img {
      position: absolute;
      object-fit: cover;
      left: 50%;
      top: 50%;
      @include transform(translateX(-50%) translateY(-50%));
    }
  }

  &__name {
    font-size: 2.4rem;
    @extend .regularFF--sec !optional;
    color: var(--cultured);
    margin-top: calc(var(--full-space)*2);
    margin-bottom: var(--full-space);
  }

  &__designation {
    font-size: 1.6rem;
    color: var(--secondary-title-color);
    margin-bottom: var(--full-space);
  }

  &__intro {
    font-size: 1.6rem;
    color: var(--secondary-title-color);
    margin-bottom: var(--full-space);
    // word-break: break-all;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    line-clamp: 3;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
  }

  &__cta {}

  &__social {
    margin-top: calc(var(--full-space)*2);
  }
}

.books {
  &__wrap {
    ul> {
      li {
        &:not(:last-child) {
          margin-bottom: calc(var(--full-space)*6);
        }

        &:not(:nth-child(odd)) {
          .books__box {
            @media screen and (min-width: $media-desktop-min) {
              flex-direction: row-reverse;
            }
          }
        }
      }
    }
  }

  &__box {
    justify-content: space-between;
    align-items: center;
    flex-direction: column-reverse;
    @extend %flexbox !optional;

    @media screen and (min-width: $media-desktop-min) {
      flex-direction: row;
    }

    &-lft,
    &-rgt {
      padding-left: var(--half-space);

      @media screen and (min-width: $media-desktop-min) {
        padding-right: var(--full-space);
        padding-left: var(--full-space);
      }
    }

    &-lft {
      width: 100%;
      padding-top: calc(var(--full-space)*2);
      padding-left: 0;

      @media screen and (min-width: $media-desktop-min) {
        width: auto;
        flex-grow: 1;
        padding-top: 0;
        padding-right: calc(var(--full-space)*4);
      }
    }

    &-rgt {
      width: 100%;

      @media screen and (min-width: $media-desktop-min) {
        width: 400px;
        flex-shrink: 0;
      }
    }
  }


  &__thumb {
    img {
      width: 100%;
      display: block;
    }
  }
}

.faqs-list {
  p {
    font-size: 1.2rem;
    color: var(--secondary-title-color);
    margin-bottom: var(--full-space);
    line-height: 1.6;

    @media screen and (min-width: $media-desktop-min) {
      font-size: 1.4rem;
    }

    &:not(:last-child) {
      margin-bottom: var(--full-space);

      @media screen and (min-width: $media-desktop-min) {
        margin-bottom: calc(var(--full-space) + var(--half-space));
      }
    }
  }
}