//popInUp
@include keyframes(popInUp) {
  0% {
    @include opacity(0);
    @include transform(translate(0%, 100%));
  }

  100% {
    @include opacity(1);
    transform-origin: 50% 0%;
    @include transform(translate(0%, 0%));
  }
}

//popOutUp
@include keyframes(popOutUp) {
  0% {
    @include opacity(1);
    @include transform(translate(0%, 0%));
  }

  100% {
    @include opacity(0);
    @include transform(translate(0%, 100%));
  }
}

//fade
@include keyframes(opacity) {
  0% {
    @include opacity(.2);
  }

  100% {
    @include opacity(1);
  }
}

//Rotate
@include keyframes(rotate) {
  0% {
    @include transform(rotateY(0deg));
  }

  100% {
    @include transform(rotateY(360deg));
  }
}

//Rotate Spin
@include keyframes(spin) {
  0% {
    @include transform(rotate(0deg));
  }

  100% {
    @include transform(rotate(360deg));
  }
}

//toast
@include keyframes(toast) {
  0% {
    @include transform(translateY(0%));
  }

  100% {
    @include transform(translateY(100%));
  }
}

// zoomIn
@include keyframes(zoomIn) {
  from {
    @include opacity(0);
    @include transform(scale3d(0.3, 0.3, 0.3));
  }

  50% {
    @include opacity(1);
  }
}

// New Added by JS
@include keyframes (pulse) {
  0% {
    @include transform(scaleX(1));
  }

  50% {
    @include transform(scale3d(1.17, 1.17, 1.17));
  }

  to {
    @include transform(scaleX(1));
  }
}

@include keyframes (live--pulse) {
  0% {
    @include transform(scaleX(.95));
    @include box-shadow(0, 0, 0, 0, rgba(blue, .7));
  }

  70% {
    @include transform(scaleX(1));
    @include box-shadow(0, 0, 0, 8px, rgba(blue, 0));
  }

  100% {
    @include transform(scaleX(.95));
    @include box-shadow(0, 0, 0, 0, rgba(blue, 0));
  }
}

@include keyframes (bgPos) {
  0% {
    background-position: 0%;
  }

  100% {
    background-position: 100%;
  }
}

@include keyframes (bounce) {


  0%,
  20%,
  53%,
  to {
    -webkit-animation-timing-function: cubic-bezier(.215, .61, .355, 1);
    animation-timing-function: cubic-bezier(.215, .61, .355, 1);
    -webkit-transform: translateZ(0);
    transform: translateZ(0)
  }

  40%,
  43% {
    -webkit-animation-timing-function: cubic-bezier(.755, .05, .855, .06);
    animation-timing-function: cubic-bezier(.755, .05, .855, .06);
    -webkit-transform: translate3d(0, -30px, 0) scaleY(1.1);
    transform: translate3d(0, -30px, 0) scaleY(1.1)
  }

  70% {
    -webkit-animation-timing-function: cubic-bezier(.755, .05, .855, .06);
    animation-timing-function: cubic-bezier(.755, .05, .855, .06);
    -webkit-transform: translate3d(0, -15px, 0) scaleY(1.05);
    transform: translate3d(0, -15px, 0) scaleY(1.05)
  }

  80% {
    -webkit-transition-timing-function: cubic-bezier(.215, .61, .355, 1);
    transition-timing-function: cubic-bezier(.215, .61, .355, 1);
    -webkit-transform: translateZ(0) scaleY(.95);
    transform: translateZ(0) scaleY(.95)
  }

  90% {
    -webkit-transform: translate3d(0, -4px, 0) scaleY(1.02);
    transform: translate3d(0, -4px, 0) scaleY(1.02)
  }

}

@include keyframes (slideInLeft) {
  0% {
    -webkit-transform: translate3d(-100%, 0, 0);
    transform: translate3d(-100%, 0, 0);
    visibility: visible
  }

  to {
    -webkit-transform: translateZ(0);
    transform: translateZ(0)
  }
}

@include keyframes (slideInRight) {
  0% {
    -webkit-transform: translate3d(100%, 0, 0);
    transform: translate3d(100%, 0, 0);
    visibility: visible
  }

  to {
    -webkit-transform: translateZ(0);
    transform: translateZ(0)
  }
}

@include keyframes (zoomInLeft) {
  0% {
    opacity: 0;
    -webkit-transform: scale3d(.1, .1, .1) translate3d(-1000px, 0, 0);
    transform: scale3d(.1, .1, .1) translate3d(-1000px, 0, 0);
    -webkit-animation-timing-function: cubic-bezier(.55, .055, .675, .19);
    animation-timing-function: cubic-bezier(.55, .055, .675, .19)
  }

  60% {
    opacity: 1;
    -webkit-transform: scale3d(.475, .475, .475) translate3d(10px, 0, 0);
    transform: scale3d(.475, .475, .475) translate3d(10px, 0, 0);
    -webkit-animation-timing-function: cubic-bezier(.175, .885, .32, 1);
    animation-timing-function: cubic-bezier(.175, .885, .32, 1)
  }
}

@include keyframes (puffIn) {
  0% {
    opacity: 0;
    transform-origin: 50% 50%;
    transform: scale(2, 2);
    filter: blur(2px);
  }

  100% {
    opacity: 1;
    transform-origin: 50% 50%;
    transform: scale(1, 1);
    filter: blur(0px);
  }
}

@include keyframes(cardScale) {
  0% {
    opacity: 0;
    left: -100%;
    opacity: 0;
    @include transform(scale(.3) rotateY(80deg));
  }

  50% {
    opacity: 0.5;
    left: 20%;
    @include transform(scale(1.5) rotateY(45deg));
  }

  100% {
    opacity: 1;
    left: 50%;
    @include transform(scale(.9) rotateY(0deg) translateX(-50%));
  }
}

@include keyframes(diagonal) {
  0% {
    transform: translatex(-50%) translatey(-50%)
  }

  50% {
    transform: translatex(0) translatey(0);
  }

  100% {
    transform: translatex(-50%) translatey(-50%)
  }
}

@include keyframes(diagonal-reverse) {
  0% {
    transform: translatex(50%) translatey(50%)
  }

  50% {
    transform: translatex(0) translatey(0);
  }

  100% {
    transform: translatex(50%) translatey(50%)
  }
}

@include keyframes(bounceAlpha) {
  0% {
    opacity: 1;
    transform: translateX(0px) scale(1);
  }

  25% {
    opacity: 0;
    transform: translateX(10px) scale(0.9);
  }

  26% {
    opacity: 0;
    transform: translateX(-10px) scale(0.9);
  }

  55% {
    opacity: 1;
    transform: translateX(0px) scale(1);
  }
}

@include keyframes(borderTransform) {

  0%,
  100% {
    border-radius: 63% 37% 54% 46% / 55% 48% 52% 45%;
  }

  14% {
    border-radius: 40% 60% 54% 46% / 49% 60% 40% 51%;
  }

  28% {
    border-radius: 54% 46% 38% 62% / 49% 70% 30% 51%;
  }

  42% {
    border-radius: 61% 39% 55% 45% / 61% 38% 62% 39%;
  }

  56% {
    border-radius: 61% 39% 67% 33% / 70% 50% 50% 30%;
  }

  70% {
    border-radius: 50% 50% 34% 66% / 56% 68% 32% 44%;
  }

  84% {
    border-radius: 46% 54% 50% 50% / 35% 61% 39% 65%;
  }
}

@include keyframes(scaleLoader) {
  0% {
    transform: scale(1);
  }

  80%,
  100% {
    transform: scale(0);
  }
}