* {
  margin: 0;
  padding: 0;
  image-rendering: -moz-crisp-edges;
  image-rendering: -o-crisp-edges;
  image-rendering: -webkit-optimize-contrast;
  image-rendering: crisp-edges;
  -ms-interpolation-mode: nearest-neighbor;
  -webkit-backface-visibility: hidden;
  @include box-sizing(border-box);

  @media screen and (min-width: $media-mobile-max) {
    // For firefox browser
    scrollbar-width: thin;
    scrollbar-color: rgba(var(--white-rgb), .7) rgba(var(--black-rgb), 1);

    &::-webkit-scrollbar {
      width: 6px;
    }

    &::-webkit-scrollbar-track {
      background: rgba(var(--black-rgb), 1);
    }

    &::-webkit-scrollbar-thumb {
      background: rgba(var(--white-rgb), .7);
    }

    &::-webkit-scrollbar-thumb:hover {
      background: rgba(var(--white-rgb), .7);
    }
  }
}

body {
  position: relative;
  overflow-x: hidden !important;
  touch-action: manipulation;
  background-color: var(--body-bg);

  &.ar {
    direction: rtl;
  }
}

html,
body {
  height: 100%;
  font-size: var(--base-font-size);
  /* Needed for container's min-height  */
}

html {
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
  -webkit-text-size-adjust: 100%;

  &:focus-within {
    scroll-behavior: smooth;
  }
}

.main {
  position: relative;
  line-height: normal;
  font-size: 1.6rem;
  min-height: 100vh;
  @extend .regularFF !optional;
  color: var(--default);
  font-smoothing: antialiased;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-touch-callout: none;
  min-height: calc(100vh - 80rem);
  // -webkit-user-select: none;
  // -khtml-user-select: none;
  // -moz-user-select: none;
  // -ms-user-select: none;
  // user-select: none;
  backface-visibility: hidden;
  font-variant-ligatures: no-common-ligatures;
  @include animation("opacity .15s both");

  &__wrap {
    padding-right: 2rem;
    padding-left: 2rem;
  }

  &__container {
    width: 100%;
    margin: 0 auto;
    // background-color: gold;

    @media screen and (min-width: $media-mobile-min) {
      // background-color: red;
    }

    @media screen and (min-width: $media-tablets-min) {
      // background-color: green;
    }

    @media screen and (min-width: $media-desktop-min) {
      // background-color: blue;
      max-width: 1290px;
    }

    @media screen and (min-width: $media-lg-desktop-min) {
      // background-color: yellow;
    }

    @media screen and (min-width: $media-xlg-desktop-min) {
      // background-color: gray;
    }

    &--fluid {
      width: 100%;
      padding-left: var(--full-space);
      padding-right: var(--full-space);
      @extend .bgSize--cover !optional;

      @media screen and (min-width: $media-desktop-min) {}
    }
  }

  ul,
  ol {
    margin: 0;
    padding: 0;
    font-size: 0;
    list-style: none;
  }

  em {
    font-style: normal;
  }

  a {
    cursor: pointer;
    text-decoration: none;
  }

  b,
  strong {
    @extend .boldFF !optional;
  }

  @media screen and (-webkit-min-device-pixel-ratio: 0) {

    select:focus,
    textarea:focus,
    input:focus {
      font-size: 1.6rem;
    }

    input:focus,
    textarea:focus {
      @include placeholder {
        font-size: 1.6rem;
      }
    }
  }

  input:focus,
  textarea:focus {
    @include placeholder {
      color: var(--transparent);
      outline: none;
      box-shadow: none;
    }
  }

  input,
  textarea {
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;

    @include placeholder {
      -webkit-appearance: none;
      -moz-appearance: none;
      appearance: none;
      color: var(--black);
      outline: none;
      box-shadow: none;
      font-family: $regular_font-family;
      font-weight: $regular_font-weight;
      font-style: italic;
    }
  }

  :focus {
    outline: none;
  }

  *,
  *::before,
  *::after {
    -webkit-tap-highlight-color: rgba(var(--black-rgb), 0);
    -webkit-tap-highlight-color: transparent;
    outline: 0;
    @include box-sizing(border-box);
  }
}

//pop-ups
.popup {
  &__wrap {
    z-index: -9999;
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    max-width: 500px;
    width: 100%;
    height: 100%;
    padding: 0 var(--full-space);
    overflow: auto;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    overflow: hidden;
    @extend %flexbox !optional;

    @media screen and (min-width: $media-desktop-min) {
      max-width: 100%;
    }
  }

  &__container {
    overflow: hidden;
    max-height: calc(100vh - 40px);
    align-items: center;
    max-width: 650px;
    width: 100%;
    margin: 0 auto;
    position: relative;
    z-index: 1;
    background: linear-gradient(270.52deg, var(--popup-gradient-start) 9.71%, var(--popup-gradient-stop) 84.7%);
    border: 1px solid rgba(var(--white-rgb), 0.1);
    align-items: center;
    justify-content: center;
    flex-direction: column;
    @extend %flexbox !optional;
    @extend .borRad10 !optional;
    @include transform(translate(0, -25%) scale(0));
    @include transition(transform 0.3s ease-in);


    @media screen and (min-width: $media-desktop-min) {
      max-width: 700px;
    }
  }

  &__close {
    position: absolute;
    top: 0;
    left: auto;
    right: 0;
    width: 3.6rem;
    height: 3.6rem;
    cursor: pointer;
    z-index: 2;
    justify-content: center;
    align-items: center;
    @extend %flexbox !optional;

    .btn__adRe {
      @include transform(rotate(45deg));
      background-color: transparent;

      &::after {
        height: 100%;
      }

      &::before {
        width: 100%;
      }

      &::after,
      &::before {
        background-color: var(--white);
      }
    }
  }

  &__heading {
    position: relative;
    font-size: 4rem;
    @extend .extraBoldFF !optional;
    text-align: center;
    margin-bottom: var(--full-space);
    color: var(--popup-title);

    @media screen and (max-width: $media-mobile-min) {
      font-size: 3rem;
    }

    @media screen and (min-width: $media-mobile-max) {
      font-size: 6rem;
    }

    &-sub {
      font-size: 1.6rem;
      @extend .regularFF !optional;
      text-align: center;
      color: var(--white);

      @media screen and (max-width: $media-mobile-min) {
        font-size: 1.4rem;
      }

      @media screen and (min-width: $media-mobile-max) {
        font-size: 2rem;
      }

      b {
        @extend .extraBoldFF !optional;
      }
    }
  }

  &__head,
  &__foot,
  &__body {
    padding: var(--full-space);
    width: 100%;

    @media screen and (max-width: $media-mobile-max) {
      padding-left: var(--half-space);
      padding-right: var(--half-space);
    }
  }

  &__head,
  &__foot {
    position: relative;
  }

  &__head {
    min-height: 60px;
  }

  &__body {
    max-height: calc(100vh - 60px);
    -webkit-overflow-scrolling: touch;
    flex-grow: 1;
    flex-shrink: 1;
    overflow: auto;
  }

  &__foot {
    padding-bottom: calc(var(--full-space) *2);
    justify-content: center;
    align-items: center;
    @extend %flexbox !optional;

    .btn {
      min-width: 25rem;
      margin-left: var(--half-space);
      margin-right: var(--half-space);

      @media screen and (max-width: $media-mobile-min) {
        &:not(:last-child) {
          margin-bottom: var(--full-space);
        }
      }
    }
  }

}

.bodyNoScroll {
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  overflow: hidden;

  @media screen and (max-width: $media-desktop-min) {
    position: fixed;
  }
}

.frm-errMsg {
  color: var(--red);
  font-size: 1.2rem;
  @extend .boldrFF !optional;
  margin-top: .4rem;
}

.fb-reverse {
  flex-direction: row-reverse;
}

//Flexbox Grid
.fb {
  @extend %flexbox !optional;
}

//table style start here
.tbl {
  display: table;
  width: 100%;

  &__head,
  &__body,
  &__foot {
    display: table-header-group;
  }

  &__row {
    display: table-row;
  }

  &__cell {
    display: table-cell;
    vertical-align: middle;
  }
}

//
.noDataFound {
  color: yellow;
  font-size: 1.4rem;
  text-align: center;
  @extend .regularFF !optional;
  padding-bottom: var(--full-space);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100px;
  @extend %flexbox !optional;

  @media screen and (min-width: $media-mobile-max) {
    font-size: 1.6rem;
  }

  &__text {
    padding-bottom: var(--full-space);
  }

  &__thumb {}

  &__cta {
    cursor: pointer;
    text-align: center;
    font-size: 14px;
    @extend .boldFF !optional;
    padding: 20px 16px 0;
    color: var(--black);

    @media screen and (min-width: $media-mobile-max) {
      font-size: 16px;
    }
  }
}

//Buttons
.btn {
  box-shadow: none;
  outline: none;
  border: none;
  pointer-events: all;
  position: relative;
  overflow: hidden;
  color: var(--white);
  height: 3.5rem;
  min-width: 15rem;
  text-align: center;
  margin: 0 auto;
  cursor: pointer;
  font-size: 1.6rem;
  line-height: 1;
  @extend .boldFF !optional;
  justify-content: center;
  align-items: center;
  padding: 0 var(--full-space);
  @extend %inline-flex;
  @extend .borRad100 !optional;


  @media screen and (min-width: $media-desktop-min) {
    min-width: 182px;
    // min-height: 5.4rem;
  }

  span {
    position: relative;
    z-index: 1;
  }

  &.disabled {
    pointer-events: none;
    opacity: 0.5;
  }

  &__primary,
  &__secondary {
    overflow: hidden;
  }

  &__primary {
    &:active {}
  }

  &__secondary {
    &:active {}
  }

  &__white {
    background-color: var(--cultured);
    color: var(--black);

    @media screen and (min-width: $media-desktop-min) {
      &:hover {
        background-color: var(--white);
      }
    }

    &--stroke {
      background-color: transparent;
      border: 1px solid var(--cultured);
      color: var(--cultured);

      @media screen and (min-width: $media-desktop-min) {
        &:hover {
          background-color: var(--cultured);
          color: var(--black);
        }
      }
    }
  }

  &__plusMinus {
    padding: 0;
    margin: 0;
    min-width: 6rem;
    background-color: transparent;
  }

  &__link {
    font-family: $regular_font-family !important;
    font-weight: $regular_font-weight !important;

    span {
      text-decoration: underline;
    }
  }
}

//Form lable
.lbl {
  margin-bottom: .4rem;
  font-size: 1.4rem;
  color: var(--black);
  @extend .regularFF !optional;

  @media screen and (min-width: $media-mobile-max) {
    font-size: 1.6rem;
  }
}

//Form Dropdown
.drpDwn {
  position: relative;
  display: inline-block;
  width: 100%;

  &.active {
    .drpDwn__value {
      &::after {
        @include transform(translateY(-50%) rotate(45deg));
      }
    }

    .drpDwn__value {
      @include border-radius(.8rem .8rem 0 0);
    }

    .drpDwn__list {
      pointer-events: auto;
      @include opacity(1);
    }
  }

  &__value {
    cursor: pointer;
    position: relative;
    padding: var(--half-space) var(--full-space);
    padding-right: 25px;
    border: none;
    background: none;
    font-size: 1.6rem;
    color: var(--white);
    height: 39px;
    @extend .boldFF !optional;
    @include text-truncate;
    @extend .borRad10 !optional;

    &::after {
      content: "";
      position: absolute;
      left: auto;
      border-left: 1px solid var(--white);
      border-top: 1px solid var(--white);
      width: 10px;
      height: 10px;
      right: 10px;
      top: 50%;
      @include transform(translateY(-50%) rotate(220deg));
      @include transition(transform 0.4s ease-in-out);
    }

    em {
      @extend .regularFF !optional;
    }

    select {
      opacity: 0;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      display: none;

      @media screen and (max-width: $media-mobile-max) {
        display: block;
      }
    }
  }

  &__list {
    position: absolute;
    background-color: #f1ebe3;
    width: 100%;
    z-index: 3;
    left: 0;
    top: 100%;
    height: 200px;
    padding-bottom: var(--half-space);
    overflow: auto;
    pointer-events: none;
    @include opacity(0);
    @include transition(all 0.2s ease-in);

    @media screen and (max-width: $media-mobile-max) {
      display: none;
    }

    &Up {
      bottom: 40px;
      top: auto;
    }

    ul {
      li {
        font-size: 14px;
        padding: 10px 25px;
        cursor: pointer;

        &:not(:last-child) {
          border-bottom: 1px solid rgba(var(--black-rgb), 0.2);
        }
      }
    }
  }
}

//Form Input
.input {
  z-index: 1;
  position: relative;

  input {
    box-shadow: none;
    outline: none;
    // border: none;
    font-size: 1.4rem;
    color: var(--black);
    @extend .regularFF !optional;
    padding: var(--half-space) var(--full-space);
    padding-right: 35px;
    height: 50px;
    width: 100%;
    display: block;
    background: var(--white);
    border: .1rem solid #F0F0F0;
    @extend .borRad10 !optional;
  }

  &__info {
    font-size: 12px;
    color: var(--black);
    font-style: italic;
    @extend .regularFF !optional;
  }

  &--pwd,
  &--clear,
  &--search {
    position: relative;

    .input--icon {
      justify-content: center;
      align-items: center;
      @extend %flexbox !optional;

      &::after {
        font-family: 'ycf' !important;
        color: var(--white);
        justify-content: center;
        align-items: center;
        @extend %flexbox !optional;
      }
    }
  }

  &--pwd {
    .input--icon {
      &::after {
        content: "\e906";
      }

      &-active {
        &::after {
          content: "\e917";
        }
      }
    }
  }

  &--clear {
    .input--icon {
      &::after {
        content: "\e903";
        width: 14px;
        height: 14px;
        font-size: 6px;
        @include border-radius(100px);
      }
    }
  }

  &--search {
    input {
      padding-left: 40px;
    }

    &::after {
      content: "\e900";
      font-family: 'ycf' !important;
      width: 16px;
      height: 16px;
      position: absolute;
      left: 12px;
      top: 50%;
      color: var(--black);
      @include transform(translateY(-50%));
    }
  }

  &--icon {
    cursor: pointer;
    position: absolute;
    left: auto;
    right: 0;
    height: 100%;
    width: 35px;
    top: 0;
  }

  &--success {
    input {
      border-color: var(--green) !important;
    }
  }

  &--danger {
    input {
      color: var(--red);
      border-color: var(--red) !important;
    }
  }
}

//Checkbox button
.checkBox {
  position: relative;

  [type="checkbox"] {

    &:checked,
    &:not(:checked) {
      position: absolute;
      left: -9999px;

      &+label {
        position: relative;
        padding-left: 28px;
        cursor: pointer;
        line-height: normal;
        display: block;
        font-size: 12px;
        @extend .regularFF !optional;

        @media screen and (min-width: $media-mobile-max) {
          font-size: 14px;
        }

        &::after,
        &::before {
          position: absolute;
          left: 0;
          top: 50%;
          width: 20px;
          height: 20px;
          @extend .borRad100 !optional;
        }

        &::before {
          background: rgba(yellow, .2);
          content: "";
          border: 1px solid yellow;
          transform-origin: center center;
          @include transform(translateY(-50%));
        }

        &::after {
          font-family: 'ycf' !important;
          content: "\e90e";
          color: var(--white);
          font-size: 10px;
          transform-origin: center center;
          justify-content: center;
          align-items: center;
          @include transition(all 0.2s ease);
          @extend %flexbox !optional;
        }

        a {
          font-size: 12px;
          color: var(--white);
          @extend .boldFF !optional;
        }
      }
    }

    &:checked {
      &+label {
        // color: var(--black);
      }
    }

    &:not(:checked)+label::after {
      opacity: 0;
      @include transform(scale(0) translateY(-50%));
    }

    &:checked+label::after {
      background: yellow;
      opacity: 1;
      @include transform(scale(1) translateY(-50%));
    }

    &:checked+label::before {
      @include transition(all 0.2s ease);
    }

    &:disabled {

      :checked+label::before,
      :not(:checked)+label::before {
        border-color: var(--disable);
        background-color: var(--disable);
      }
    }
  }

  &:disabled:checked+label::after {
    background: rgba(var(--disable-rgb), .2);
  }
}

// radio button
.radioBtn {
  position: relative;
  background: #f5f5f5;
  margin: 0 -16px;
  padding: 12px 16px;
  justify-content: space-between;
  align-items: center;
  @extend %flexbox !optional;

  &__wrap {
    position: relative;
    justify-content: flex-end;
    align-items: center;
    @extend %inline-flex;
  }

  input {
    cursor: pointer;
    height: 100%;
    width: 100%;
    margin: 0;
    opacity: 0;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;
    @include transition(all 0.3s ease-in-out);

    &[type="checkbox"]:checked~.radioBtn__action {
      background-color: blue;
    }

    &[type="checkbox"]:checked~.radioBtn__action .radioBtn__toggle {
      left: 14px;
      background-color: var(--black);
    }

    &[type="checkbox"]:checked~.radioBtn__lbl {}
  }

  &__lbl,
  &__action {
    padding-right: 10px;
  }

  &__action {
    padding: 0;
    position: relative;
    width: 34px;
    height: 14px;
    background-color: rgba(#969696, 0.38);
    @include border-radius(20px);
  }

  &__lbl {
    font-size: 12px;
    color: yellow;
  }

  &__toggle {
    background: var(--white);
    display: inline-block;
    height: 20px;
    width: 20px;
    position: absolute;
    left: 0;
    top: 50%;
    box-shadow: 0 1px 3px 0 rgba(var(--black-rgb), 0.2), 0 2px 1px -1px rgba(var(--black-rgb), 0.12), 0 1px 1px 0 rgba(var(--black-rgb), 0.14);
    @include transform(translateY(-50%));
    @include border-radius(100px);
    @include transition(all 0.1s linear);
  }
}

.form {
  margin-bottom: 20px;

  &:last-of-type {
    margin-bottom: 0;
  }

  &__control {
    margin-bottom: 20px;

    &:last-of-type {
      margin-bottom: 0;
    }
  }
}

//animations transition
.fade {
  &.in {
    z-index: 99999;
    background: rgba(var(--black-rgb), 0.6);
    @include opacity(1);
    @include transition(opacity 0.1s linear, padding-top 0.1s linear);

    &.mint__overlay,
    .popup__container {
      @include animation("popInUp .15s both");
    }
  }

  &.out {
    pointer-events: none;
    @include opacity(0);
    @include transition(opacity 0.1s linear, padding-top 0.1s linear);

    &.mint__overlay,
    .popup__container {
      @include animation("popOutUp .15s both");
    }
  }

  &.bg-dark {
    &.in {
      background: rgba(var(--black-rgb), 0.8) !important;
    }
  }
}

// Accordion
.accordion {
  margin: var(--full-space) 0;
  position: relative;

  &__wrap {
    padding: 0 10px;
    position: relative;
  }

  &__box {
    position: relative;
    overflow: hidden;
    color: var(--cultured);
    font-size: 1.6rem;
    @extend .regularFF !optional;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: auto;
      bottom: 0;
      width: 100%;
      height: 0.1rem;
      opacity: .5;
      @extend .stroke-bg !optional;
    }

    &.active {
      background-color: var(--secondary-color);

      .accordion__head {
        &::after {
          background-image: url(../images/minus.svg?v=#{$image-version});
        }
      }

      .accordion__body {
        display: block;
      }
    }

    &.noClick {
      cursor: default;

      .accordion__head {
        cursor: default;

        &::after {
          display: none;
        }
      }

      .accordion__body {
        cursor: default;
        display: block;
      }
    }
  }

  &__head,
  &__body {
    padding: var(--full-space) var(--half-space);

    @media screen and (min-width: $media-desktop-min) {
      padding: var(--full-space);
    }
  }

  &__head {
    font-size: 1.4rem;
    cursor: pointer;
    position: relative;
    padding-right: calc(var(--full-space)*3);

    @media screen and (min-width: $media-desktop-min) {
      font-size: 1.8rem;
    }

    &::after {
      content: "";
      position: absolute;
      left: auto;
      top: 50%;
      right: var(--half-space);
      width: 2rem;
      height: 2rem;
      background-image: url(../images/add.svg?v=#{$image-version});
      background-repeat: no-repeat;
      background-position: center center;
      justify-content: center;
      align-items: center;
      @extend %flexbox !optional;
      @extend .bgSize--contain !optional;
      @include transition(background-image 0.1s ease-in-out);
      @include transform(translateY(-50%));

      @media screen and (min-width: $media-desktop-min) {
        right: var(--full-space);
      }
    }
  }

  &__body {
    font-size: 1.4rem;
    display: none;
    padding-top: 10px;

    @media screen and (min-width: $media-desktop-min) {
      font-size: 1.8rem;
    }

    .noDataFound {
      padding-bottom: 0;

      &__text {
        padding-bottom: 0;
      }
    }
  }

  &__loader {
    position: relative;
    min-height: 150px;
    padding: 0 !important;

    .loader {
      background: transparent;
    }
  }
}

.negativeNum {
  color: var(--red) !important;
}

.noClick {
  cursor: default !important;
}

.ad {
  justify-content: center;
  align-items: center;
  @extend %flexbox !optional;
  margin-bottom: 15px;

  img {
    width: 100%;
    height: auto;
  }
}

// border radius
.borRad0 {
  @include border-radius(0);
}

.borRad4 {
  @include border-radius(.4rem);
}

.borRad6 {
  @include border-radius(.6rem);
}

.borRad10 {
  @include border-radius(.8rem);
}

.borRad10 {
  @include border-radius(1rem);
}

.borRad100 {
  @include border-radius(100px);
}

.borRad20--tr {
  @include border-radius(2rem 2rem 0 0);
}

//background size
.bgSize--cover {
  @include background-size(cover);
}

.bgSize--contain {
  @include background-size(contain);
}

// Font-family
.regularFF {
  font-family: $regular_font-family;
  font-weight: $regular_font-weight;
}

.regularFF--sec {
  font-family: $regular_font-family--sec;
  font-weight: $regular_font-weight;
}

.boldFF--sec {
  font-family: $bold_font-family--sec;
  font-weight: $bold_font-weight;
}

// .mediumFF {
//   font-family: $medium_font-family;
//   font-weight: $medium_font-weight;
// }

.boldFF {
  font-family: $bold_font-family;
  font-weight: $bold_font-weight;
}

// .extraBoldFF {
//   font-family: $black_font-family;
//   font-weight: $black_font-weight;
// }

.stroke-bg {
  @include linearGradient(left, #B8B8B8 0%, #B8B8B8 50%, rgba(#F4F4F4, 0) 100%);
}

// add/remove cta btn used for playerList
.btn__adRe {
  position: relative;
  width: 2rem;
  height: 2rem;
  cursor: pointer;
  background-color: var(--white);
  @extend .borRad100 !optional;
  @include transition(all 0.1s linear);

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 50%;
    left: 0;
    right: 0;
    margin: 0 auto;
    background: var(--black);
    @include transform(translateY(-50%));
    @include transition(all 0.1s linear);
  }

  &::before {
    width: 10px;
    height: 2px;
  }

  &::after {
    height: 10px;
    width: 2px;
  }
}

// swiper over-ride
.swiper-wrapper,
.swiper-slide {
  height: auto;
}

.hidden {
  display: none !important;
}

// orientation landscape
@media screen and (max-width: $media-desktop-max) and (orientation: landscape) {
  body {
    overflow: hidden;
  }
}

.orientation {
  &__wrap {
    z-index: 999999;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: var(--black);
    justify-content: center;
    align-items: center;
    display: none;

    @media screen and (max-width: $media-desktop-min) and (orientation: landscape) {
      @include flexbox;
    }
  }

  &__box {
    text-align: center;
  }

  &__thumb {
    margin-bottom: 10px;
  }

  &__text {
    color: var(--white);
    @extend .mediumFF !optional;
    font-size: 14px;
  }
}

// common styling
.section {
  &-row {
    min-height: calc(var(--window-inner-height) - var(--footer));
    padding-top: calc(var(--full-space)*7);
    padding-bottom: calc(var(--full-space)*1);

    @media screen and (min-width: $media-desktop-min) {
      padding-top: calc(var(--full-space)*12);
      padding-bottom: calc(var(--full-space)*3);
    }

    &-inner {
      padding-top: calc(var(--full-space)*7);
      padding-bottom: calc(var(--full-space)*1);

      @media screen and (min-width: $media-desktop-min) {
        padding-top: calc(var(--full-space)*12);
        padding-bottom: calc(var(--full-space)*3);
      }
    }
  }

  &-lbl {
    font-size: 1.6rem;
    color: var(--secondary-title-color);
    @extend .regularFF !optional;
  }

  &-heading {
    position: relative;
    color: var(--white);
    font-size: 3rem;
    font-style: italic;
    padding-bottom: calc(var(--full-space) *1);
    margin-bottom: calc(var(--full-space)*1);
    @extend .regularFF--sec !optional;

    @media screen and (min-width: $media-desktop-min) {
      padding-bottom: calc(var(--full-space) *2);
      margin-bottom: calc(var(--full-space) *2);
      font-size: 4.8rem;
    }

    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: auto;
      bottom: 0;
      width: 100%;
      height: 0.2rem;
      @extend .stroke-bg !optional;
    }
  }

  &__para {
    p {
      font-size: 1.6rem;
      line-height: 1.6;
      color: var(--secondary-title-color);
      @extend .regularFF !optional;

      &:not(:last-child) {
        margin-bottom: var(--full-space);
      }
    }

    ol {
      margin-bottom: var(--full-space);
      margin-left: 2rem;
      list-style: disc;
      list-style-position: outside;

      li {
        font-size: 1.6rem;
        line-height: 1.6;
        color: var(--secondary-title-color);
        @extend .regularFF !optional;

        &:not(:last-child) {
          margin-bottom: var(--half-space);
        }
      }
    }
  }
}