.header {
  &__wrap {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    // position: relative;
    z-index: 1;
    height: 6rem;
    padding: var(--full-space);
    background-color: var(--black);

    @media screen and (min-width: $media-desktop-min) {
      height: 8rem;
    }
  }

  &__box {
    justify-content: space-between;
    align-items: center;
    @extend %flexbox !optional;
  }

  &__logo {
    width: 7.6rem;
    height: 3rem;
    z-index: 1;

    @media screen and (min-width: $media-desktop-min) {
      width: 16.8rem;
      height: 5rem;
    }

    a {
      display: block;
    }

    img {
      display: block;
      width: 100%;
      filter: drop-shadow(3px 3px 5px rgba(var(--black-rgb), .4));
    }
  }

  &__nav {
    @media screen and (max-width: $media-mobile-min) {
      position: fixed;
      padding-top: var(--full-space);
      left: -100%;
      top: 6rem;
      width: 100%;
      height: 100vh;
      background-color: var(--black);
      @include transition(left 0.2s linear);

      &.active {
        left: 0;
      }
    }

    ul {
      flex-direction: column;
      justify-content: flex-start;
      align-items: flex-start;
      @extend %flexbox !optional;

      @media screen and (min-width: $media-desktop-min) {
        align-items: center;
        flex-direction: row;
        position: relative;
      }

      li {
        font-size: 2.6rem;
        font-style: italic;
        width: 100%;
        @extend .regularFF--sec !optional;

        a {
          position: relative;
          color: rgba(var(--white-rgb), .64);
          display: block;
          padding: calc(var(--full-space) *2) 0;
          margin: 0 var(--full-space);

          &::before {
            content: '';
            position: absolute;
            left: 0;
            top: auto;
            bottom: 0;
            width: 100%;
            height: 0.2rem;
            @extend .stroke-bg !optional;
          }

          &.active {
            color: var(--white);
          }
        }

        @media screen and (min-width: $media-desktop-min) {
          width: auto;
          font-size: 1.6rem;
          font-style: normal;
          font-family: $regular_font-family;
          font-weight: $regular_font-weight;

          &:not(:last-child) {
            margin-right: calc(var(--full-space)*4);
          }

          a {
            padding: 0;
            margin: 0;
            text-transform: uppercase;

            &::before {
              display: none;
            }

            &:hover {
              color: var(--white);
            }
          }
        }
      }
    }
  }

  &__hamburger {
    &-menu {
      cursor: pointer;
      display: block;

      @media screen and (min-width: $media-desktop-min) {
        display: none;
      }

      .menu {

        &-bar1,
        &-bar2,
        &-bar3 {
          width: 2.5rem;
          height: 0.2rem;
          background-color: var(--white);
          margin: 0.7rem 0;
          transition: 0.4s;
        }

        &-bar2 {
          width: 2rem;
          margin-left: auto;
        }
      }

      &.active {
        .menu {
          &-bar1 {
            transform: rotate(-45deg) translate(-0.7rem, 0.8rem);
          }

          &-bar2 {
            opacity: 0;
          }

          &-bar3 {
            transform: rotate(45deg) translate(-0.6rem, -0.8rem);
          }
        }
      }
    }
  }
}