.footer {
  &__wrap {
    color: rgba(var(--white-rgb), .64);
    font-size: 1.2rem;
    min-height: var(--footer);
    background-color: var(--black);
    // text-transform: uppercase;
    justify-content: flex-start;
    align-items: center;
    @extend %flexbox !optional;

    @media screen and (min-width: $media-desktop-min) {
      font-size: 1.6rem;
    }
  }

  &__box {
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    @extend %flexbox !optional;

    @media screen and (min-width: $media-desktop-min) {
      flex-direction: row;
    }
  }

  &__logo {
    width: 6rem;
    object-fit: contain;
    margin: var(--full-space) 0;

    img {
      display: block;
      width: 100%;
    }
  }

  &__copyrite {
    padding: 0 var(--full-space) .3rem;
    position: relative;

    @media screen and (min-width: $media-desktop-min) {
      padding-bottom: 0;

      // &::before {
      //   content: "";
      //   position: absolute;
      //   left: auto;
      //   right: 0;
      //   top: 0;
      //   width: 1px;
      //   height: 100%;
      //   background-color: rgba(var(--white-rgb), .64);
      // }
    }
  }

  &__nav {
    ul {
      justify-content: flex-start;
      align-items: center;
      @extend %flexbox !optional;

      li {
        padding: 0 var(--half-space);
        position: relative;

        &:not(:last-child) {
          &::before {
            content: "";
            position: absolute;
            left: auto;
            right: 0;
            top: 0;
            width: 1px;
            height: 100%;
            background-color: rgba(var(--white-rgb), .64);
          }
        }

        a {
          font-size: 1.2rem;
          color: rgba(var(--white-rgb), .64);

          @media screen and (min-width: $media-desktop-min) {
            font-size: 1.6rem;

            &:hover {
              color: var(--white);
            }
          }
        }
      }
    }
  }

  &__socials {
    flex-grow: 1;
    padding: var(--full-space);

    ul {
      justify-content: flex-end;
      align-items: center;
      @extend %flexbox !optional;

      li {
        &:not(:last-child) {
          margin-right: var(--full-space);
        }
      }
    }
  }
}