//Colors
$client-primary-color: #6001D2;
$client-secondary-color: #232526;

// $font-icon: "waf-font-icon";
$image-version: "2.0";
// $font-version: "44622585";

:root {
  --transparent: transparent;
  --googlePlus: #dd4b39;
  --facebook: #1977f3;
  --twitter: #2daae1;
  --whatsApp: #26ce46;
  --instagram: #125688;

  --body-bg: #353535;
  --default: #181818;
  --default-rgb: 70, 78, 86;
  --black: #00011B;
  --black-rgb: 0, 1, 27;
  --white: #ffffff;
  --white-rgb: 255, 255, 255;
  --gray: #2D2D2D;
  --gray-rgb: 45, 45, 45;
  --red: #EB5757;
  --green: #1AC567;
  --disable: #C7CDD2;
  --disable-rgb: 199, 205, 210;

  //client common color
  --cultured: #F4F4F4;
  --cultured-rgb: 245, 245, 245;
  --primary-dark-color: #1D1D1D;
  --primary-dark-color-rgb: 29, 29, 29;
  --secondary-color: #232526;
  --secondary-color-rgb: 35, 37, 38;
  --secondary-title-color: #B8B8B8;
  --secondary-tile-color-rgb: 184, 184, 184;
  --platinum: #E3E3D9;
  --gold: #FFD700;
  --silver: #848482;
  --bronze: #CD7F32;

  // all variables
  --base-font-size: 62.5%;
  --vh: 1vh;
  --window-inner-height: calc(var(--vh, 1vh) * 100);
  --full-space: 1.5rem;
  --half-space: 0.75rem;
  --full-space-negative: calc(var(--full-space) * -1);
  --half-space-negative: calc(var(--half-space) * -1);
  --border-radius: 1.5rem;
  --half-border-radius: 0.75rem;
  --footer: 8rem;
}



//Event theme
.event-theme {
  :root {
    --theme-default: #d2091b;
    --theme-default-rgb: 210, 9, 27;
  }
}

.white-theme {
  :root {}
}


// mobile ex-small
$media-mobile-ex-sm-min: 320px;
$media-mobile-ex-sm-max: 321.98px;

// mobile small
$media-mobile-sm-min: 375px;
$media-mobile-sm-max: 374.98px;

// mobile
$media-mobile-min: 576px;
$media-mobile-max: 575.98px;

// tablets
$media-tablets-min: 768px;
$media-tablets-max: 767.98px;

// desktop
$media-desktop-min: 992px;
$media-desktop-max: 991.98px;

// lg-desktop
$media-lg-desktop-min: 1200px;
$media-lg-desktop-max: 1199.98px;

// extra lg-desktop
$media-xlg-desktop-min: 1400px;
$media-xlg-desktop-max: 1399.98px;

@media screen and (min-width: $media-desktop-min) {
  :root {}
}

@media screen and (min-width: $media-xlg-desktop-min) {
  :root {}
}